"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { ChevronDown, ChevronRight, Grid3x3, Trash2, RotateCcw, Check, Upload, Plus } from "lucide-react"

interface Item {
  id: string
  name: string
  color: string
  length: number
  width: number
  height: number
  weight: number
  count: number
  stackable: boolean
  turnable: boolean
  tiltable: boolean
}

interface ItemGroup {
  id: string
  name: string
  items: Item[]
}

interface ItemsPanelProps {
  onToggle: () => void
  items: Item[]
  onItemsChange: (items: Item[]) => void
  selectedItemId?: string | null
  onSelectItem?: (itemId: string | null) => void
}

export default function ItemsPanel({ onToggle, items, onItemsChange, selectedItemId, onSelectItem }: ItemsPanelProps) {
  const [groups, setGroups] = useState<ItemGroup[]>([
    {
      id: "1",
      name: "Grp. 1",
      items: [
        {
          id: "1",
          name: "Item 1",
          color: "#34D399",
          length: 100,
          width: 100,
          height: 100,
          weight: 10,
          count: 1,
          stackable: false,
          turnable: false,
          tiltable: false,
        },
      ],
    },
  ])

  const [expandedGroups, setExpandedGroups] = useState<string[]>(["1"])
  const [inputMode, setInputMode] = useState<"manual" | "file">("manual")

  useEffect(() => {
    const allItems = groups.flatMap((g) => g.items)
    onItemsChange(allItems)
  }, [groups])

  const toggleGroup = (groupId: string) => {
    setExpandedGroups((prev) => (prev.includes(groupId) ? prev.filter((id) => id !== groupId) : [...prev, groupId]))
  }

  const updateItem = (groupId: string, itemId: string, field: keyof Item, value: any) => {
    setGroups((prev) =>
      prev.map((group) =>
        group.id === groupId
          ? {
              ...group,
              items: group.items.map((item) => (item.id === itemId ? { ...item, [field]: value } : item)),
            }
          : group,
      ),
    )
  }

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (file) {
      console.log("[v0] Uploaded STEP file:", file.name)

      const reader = new FileReader()
      reader.onload = (e) => {
        console.log("[v0] File loaded, size:", file.size, "bytes")

        const newItem: Item = {
          id: Date.now().toString(),
          name: file.name.replace(".step", "").replace(".stp", ""),
          color: `#${Math.floor(Math.random() * 16777215).toString(16)}`,
          length: 120,
          width: 80,
          height: 100,
          weight: 15,
          count: 1,
          stackable: false,
          turnable: false,
          tiltable: false,
        }

        setGroups((prev) => {
          const newGroups = [...prev]
          if (newGroups[0]) {
            newGroups[0].items.push(newItem)
          }
          return newGroups
        })

        onSelectItem?.(newItem.id)
      }
      reader.readAsText(file)
    }
  }

  const addManualItem = () => {
    const newItem: Item = {
      id: Date.now().toString(),
      name: `Item ${groups[0].items.length + 1}`,
      color: `#${Math.floor(Math.random() * 16777215).toString(16)}`,
      length: 100,
      width: 100,
      height: 100,
      weight: 10,
      count: 1,
      stackable: false,
      turnable: false,
      tiltable: false,
    }

    setGroups((prev) => {
      const newGroups = [...prev]
      if (newGroups[0]) {
        newGroups[0].items.push(newItem)
      }
      return newGroups
    })

    onSelectItem?.(newItem.id)
  }

  return (
    <div className="flex h-full flex-col">
      {/* Header */}
      <div className="flex items-center justify-between border-b border-gray-200 p-4">
        <div className="flex items-center gap-2">
          <Grid3x3 className="h-5 w-5" />
          <h2 className="text-lg font-semibold text-gray-900">Items</h2>
        </div>
        <Button variant="ghost" size="icon" onClick={onToggle}>
          <ChevronRight className="h-5 w-5" />
        </Button>
      </div>

      {/* Input Mode Selector and Action Buttons */}
      <div className="border-b border-gray-200 p-4">
        <div className="mb-3 flex gap-2">
          <Button
            variant={inputMode === "manual" ? "default" : "outline"}
            size="sm"
            className="flex-1"
            onClick={() => setInputMode("manual")}
          >
            Manual Input
          </Button>
          <Button
            variant={inputMode === "file" ? "default" : "outline"}
            size="sm"
            className="flex-1"
            onClick={() => setInputMode("file")}
          >
            Upload STEP File
          </Button>
        </div>

        {inputMode === "manual" ? (
          <Button onClick={addManualItem} className="w-full" size="sm">
            <Plus className="mr-2 h-4 w-4" />
            Add New Item
          </Button>
        ) : (
          <div className="relative">
            <input
              type="file"
              accept=".step,.stp"
              onChange={handleFileUpload}
              className="absolute inset-0 z-10 cursor-pointer opacity-0"
              id="step-file-input"
            />
            <Button className="w-full" size="sm" asChild>
              <label htmlFor="step-file-input" className="cursor-pointer">
                <Upload className="mr-2 h-4 w-4" />
                Upload STEP File (.step, .stp)
              </label>
            </Button>
          </div>
        )}
      </div>

      {/* Groups List */}
      <div className="flex-1 overflow-y-auto border-b border-gray-200">
        {groups.map((group) => (
          <div key={group.id} className="border-b border-gray-100">
            <button
              onClick={() => toggleGroup(group.id)}
              className="flex w-full items-center justify-between p-3 hover:bg-gray-50"
            >
              <div className="flex items-center gap-2">
                {expandedGroups.includes(group.id) ? (
                  <ChevronDown className="h-4 w-4" />
                ) : (
                  <ChevronRight className="h-4 w-4" />
                )}
                <span className="text-sm font-medium">{group.name}</span>
                <span className="text-xs text-gray-500">group name</span>
              </div>
              <div className="flex gap-1">
                <Button variant="ghost" size="icon" className="h-7 w-7">
                  <Grid3x3 className="h-3 w-3" />
                </Button>
                <Button variant="ghost" size="icon" className="h-7 w-7">
                  <Grid3x3 className="h-3 w-3" />
                </Button>
              </div>
            </button>

            {expandedGroups.includes(group.id) && (
              <div className="bg-gray-50 p-3">
                {group.items.map((item) => (
                  <div
                    key={item.id}
                    className={`mb-2 rounded-lg border bg-white p-3 ${
                      selectedItemId === item.id ? "border-blue-400 ring-2 ring-blue-200" : "border-gray-200"
                    }`}
                  >
                    <div className="mb-3 flex items-center gap-2">
                      <div className="h-8 w-8 rounded" style={{ backgroundColor: item.color }} />
                      <Input
                        value={item.name}
                        onChange={(e) => updateItem(group.id, item.id, "name", e.target.value)}
                        className="flex-1"
                      />
                      <Button variant="ghost" size="icon" className="h-8 w-8" onClick={() => onSelectItem?.(item.id)}>
                        <Check className={`h-4 w-4 ${selectedItemId === item.id ? "text-blue-500" : ""}`} />
                      </Button>
                    </div>

                    {/* Orientation Options */}
                    <div className="mb-3 flex gap-2">
                      <Button
                        variant="outline"
                        size="icon"
                        className={`h-9 w-9 ${item.stackable ? "bg-blue-50 border-blue-300" : ""}`}
                        onClick={() => updateItem(group.id, item.id, "stackable", !item.stackable)}
                      >
                        <div className="flex flex-col gap-0.5">
                          <div className="h-1 w-4 bg-current" />
                          <div className="h-1 w-4 bg-current" />
                        </div>
                      </Button>
                      <Button
                        variant="outline"
                        size="icon"
                        className={`h-9 w-9 ${item.turnable ? "bg-blue-50 border-blue-300" : ""}`}
                        onClick={() => updateItem(group.id, item.id, "turnable", !item.turnable)}
                      >
                        <RotateCcw className="h-4 w-4" />
                      </Button>
                      <Button
                        variant="outline"
                        size="icon"
                        className={`h-9 w-9 ${item.tiltable ? "bg-blue-50 border-blue-300" : ""}`}
                        onClick={() => updateItem(group.id, item.id, "tiltable", !item.tiltable)}
                      >
                        <div className="h-4 w-4 rotate-45 border-2 border-current" />
                      </Button>
                      <Button variant="outline" size="icon" className="h-9 w-9 bg-transparent">
                        <div className="h-4 w-4 border-2 border-current" />
                      </Button>
                    </div>

                    {/* Dimensions */}
                    <div className="mb-3 grid grid-cols-3 gap-2">
                      <div>
                        <label className="mb-1 block text-xs text-gray-600">Length(cm)</label>
                        <Input
                          type="number"
                          value={item.length}
                          onChange={(e) => updateItem(group.id, item.id, "length", Number(e.target.value))}
                          className="h-9"
                        />
                      </div>
                      <div>
                        <label className="mb-1 block text-xs text-gray-600">Width(cm)</label>
                        <Input
                          type="number"
                          value={item.width}
                          onChange={(e) => updateItem(group.id, item.id, "width", Number(e.target.value))}
                          className="h-9"
                        />
                      </div>
                      <div>
                        <label className="mb-1 block text-xs text-gray-600">Height(cm)</label>
                        <Input
                          type="number"
                          value={item.height}
                          onChange={(e) => updateItem(group.id, item.id, "height", Number(e.target.value))}
                          className="h-9"
                        />
                      </div>
                    </div>

                    {/* Weight and Count */}
                    <div className="flex gap-2">
                      <div className="flex-1">
                        <label className="mb-1 block text-xs text-gray-600">Weight(kg)</label>
                        <Input
                          type="number"
                          value={item.weight}
                          onChange={(e) => updateItem(group.id, item.id, "weight", Number(e.target.value))}
                          className="h-9"
                        />
                      </div>
                      <div className="w-24">
                        <label className="mb-1 block text-xs text-gray-600">Count</label>
                        <div className="flex items-center gap-1">
                          <Input
                            type="number"
                            value={item.count}
                            onChange={(e) => updateItem(group.id, item.id, "count", Number(e.target.value))}
                            className="h-9"
                          />
                          <div className="flex flex-col gap-0.5">
                            <Button
                              variant="ghost"
                              size="icon"
                              className="h-4 w-4 p-0"
                              onClick={() => updateItem(group.id, item.id, "count", item.count + 1)}
                            >
                              ▲
                            </Button>
                            <Button
                              variant="ghost"
                              size="icon"
                              className="h-4 w-4 p-0"
                              onClick={() => updateItem(group.id, item.id, "count", Math.max(1, item.count - 1))}
                            >
                              ▼
                            </Button>
                          </div>
                        </div>
                      </div>
                      <div className="flex items-end gap-1">
                        <Button variant="ghost" size="icon" className="h-9 w-9 text-red-500 hover:text-red-600">
                          <Trash2 className="h-4 w-4" />
                        </Button>
                        <Button variant="ghost" size="icon" className="h-9 w-9">
                          <RotateCcw className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        ))}
      </div>

      {/* Instruction text when item is selected */}
      {selectedItemId && (
        <div className="border-t border-gray-200 bg-blue-50 p-3 text-center">
          <p className="text-sm font-medium text-blue-900">Click inside the container to place this item</p>
        </div>
      )}
    </div>
  )
}
