"use client"

import { Canvas } from "@react-three/fiber"
import { OrbitControls, Grid, PerspectiveCamera } from "@react-three/drei"
import Container3D from "@/components/container-3d"
import { Suspense } from "react"
import type { PlacedBox } from "@/components/cargo-loader-app"

interface ViewportCanvasProps {
  container: string | null
  items: any[]
  placedBoxes: PlacedBox[]
  selectedItemId: string | null
  onPlaceBox: (position: [number, number, number]) => void
  onSelectBox: (boxId: string) => void
}

export default function ViewportCanvas({
  container,
  items,
  placedBoxes,
  selectedItemId,
  onPlaceBox,
  onSelectBox,
}: ViewportCanvasProps) {
  return (
    <div className="h-full w-full">
      <Canvas shadows>
        <PerspectiveCamera makeDefault position={[15, 10, 15]} fov={50} />
        <OrbitControls makeDefault enableDamping dampingFactor={0.05} />

        {/* Lighting */}
        <ambientLight intensity={0.5} />
        <directionalLight position={[10, 10, 5]} intensity={1} castShadow shadow-mapSize={[2048, 2048]} />
        <directionalLight position={[-10, 10, -5]} intensity={0.3} />

        {/* Grid Floor */}
        <Grid
          args={[30, 30]}
          cellSize={1}
          cellThickness={0.5}
          cellColor="#cccccc"
          sectionSize={5}
          sectionThickness={1}
          sectionColor="#999999"
          fadeDistance={50}
          fadeStrength={1}
          position={[0, 0, 0]}
        />

        {/* 3D Container and Items */}
        <Suspense fallback={null}>
          <Container3D
            items={items}
            placedBoxes={placedBoxes}
            selectedItemId={selectedItemId}
            onPlaceBox={onPlaceBox}
            onSelectBox={onSelectBox}
          />
        </Suspense>

        {/* Measurement Axis */}
        <group position={[-8, 0, -8]}>
          {/* X Axis (Red) */}
          <mesh position={[1.5, 0.05, 0]} rotation={[0, 0, Math.PI / 2]}>
            <cylinderGeometry args={[0.02, 0.02, 3]} />
            <meshBasicMaterial color="#ff0000" />
          </mesh>
          <mesh position={[3, 0.05, 0]} rotation={[0, 0, -Math.PI / 2]}>
            <coneGeometry args={[0.08, 0.2]} />
            <meshBasicMaterial color="#ff0000" />
          </mesh>

          {/* Y Axis (Green) */}
          <mesh position={[0, 1.55, 0]}>
            <cylinderGeometry args={[0.02, 0.02, 3]} />
            <meshBasicMaterial color="#00ff00" />
          </mesh>
          <mesh position={[0, 3.1, 0]}>
            <coneGeometry args={[0.08, 0.2]} />
            <meshBasicMaterial color="#00ff00" />
          </mesh>

          {/* Z Axis (Blue) */}
          <mesh position={[0, 0.05, 1.5]} rotation={[Math.PI / 2, 0, 0]}>
            <cylinderGeometry args={[0.02, 0.02, 3]} />
            <meshBasicMaterial color="#0000ff" />
          </mesh>
          <mesh position={[0, 0.05, 3]} rotation={[Math.PI / 2, 0, 0]}>
            <coneGeometry args={[0.08, 0.2]} />
            <meshBasicMaterial color="#0000ff" />
          </mesh>
        </group>

        {/* Numbering on axes */}
        <group position={[-8, 0.1, -8]}>
          <TextLabel text="0" position={[0, 0, 0]} />
          <TextLabel text="1" position={[3, 0, 0]} />
          <TextLabel text="2" position={[0, 1.5, 0]} />
        </group>
      </Canvas>
    </div>
  )
}

function TextLabel({ text, position }: { text: string; position: [number, number, number] }) {
  return (
    <mesh position={position}>
      <meshBasicMaterial color="#333333" />
    </mesh>
  )
}
